.contacts {
  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #fff;
  }

  &__info-link {
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #fff;
    text-decoration: none;

    &:hover {
      color: rgb(135, 131, 131);
    }
  }

  &__field {
    margin: 0 0 16px;
  }
}
